body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 30px;
}

ul {
  list-style: none;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

a:focus {
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

p,
h1,
h2,
h3,
h4,
ul,
figure,
blockquote {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  visibility: hidden;
  position: absolute;
}

@font-face {
  font-family: 'Open Sans';
  src: url('../fonts/opensans-bold-webfont.woff2') format('woff2'), url('../fonts/opensans-bold-webfont.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url('../fonts/opensans-light-webfont.woff2') format('woff2'), url('../fonts/opensans-light-webfont.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url('../fonts/opensans-regular-webfont.woff2') format('woff2'), url('../fonts/opensans-regular-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Open Sans';
  src: url('../fonts/opensans-semibold-webfont.woff2') format('woff2'), url('../fonts/opensans-semibold-webfont.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

.btn {
  display: block;
  cursor: pointer;
}

.btn:focus {
  outline: none;
}

.btn--square {
  font-size: 18px;
  line-height: 30px;
  font-weight: bold;
  border-style: none;
  background-color: #2aab6d;
  color: #fff;
  text-transform: uppercase;
  padding-top: 17px;
  padding-bottom: 16px;
}

.btn--square:hover {
  background-color: #1c9b5e;
}

.btn--square:active {
  color: rgba(255, 255, 255, 0.3);
}

.btn--square:focus {
  outline: none;
  border-style: none;
}

.btn--square:disabled {
  background-color: #e5e5e5;
}

.btn--round {
  font-size: 18px;
  line-height: 18px;
  font-weight: bold;
  padding-top: 15px;
  padding-bottom: 16px;
  border-width: 3px;
  border-style: solid;
  border-bottom-left-radius: 50px;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  border-top-right-radius: 50px;
}

.btn--white {
  color: #fff;
  border-color: #fff;
}

.btn--white:hover {
  background-color: #fff;
  color: #d22856;
}

.btn--white:active {
  color: rgba(210, 40, 86, 0.3);
}

.btn--pink {
  color: #d22856;
  border-color: #d22856;
  background-color: transparent;
}

.btn--pink:hover {
  background-color: #d22856;
  color: #fff;
}

.btn--pink:active {
  color: rgba(255, 255, 255, 0.3);
}

.btn--grey {
  color: #dddfe0;
  border-color: #dddfe0;
  background-color: transparent;
}

.btn--grey:hover {
  border-color: #b5b9be;
  color: rgba(40, 54, 69, 0.3);
}

.btn--grey:active {
  background-color: #b5b9be;
  color: rgba(255, 255, 255, 0.3);
}

.page-header {
  background-color: rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 1000;
  margin-bottom: -66px;
  border-bottom: 1px solid transparent;
}

.page-header__container {
  max-width: 280px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  min-height: 66px;
  justify-content: space-between;
}

.page-header--toggled {
  background-color: #1d2632;
  border-color: #000;
}

.hamburger-menu {
  height: 24px;
  cursor: pointer;
}

.hamburger-menu__icon--ham {
  width: 50px;
  height: 24px;
  fill: #fff;
}

.hamburger-menu__icon--cross {
  width: 24px;
  height: 23px;
  fill: #fff;
}

.hamburger-menu__icon--hidden {
  display: none;
}

.hamburger-menu__icon:hover {
  fill: #d22856;
}

.hamburger-menu__icon:active {
